import { instance, apiKey } from './api';

interface Iweather {
	description: string;
	icon: string;
	id: number;
	main: string;
}

type GetWeatherType = {
	base: string;
	clouds: { all: number };
	cod: number;
	coord: { lon: number, lat: number };
	dt: number;
	id: number;
	main: {
		temp: number;
		feels_like: number;
		temp_min: number;
		temp_max: number;
		pressure: number;
		humidity: number;
	};
	name: string;
	sys: {
		type: number;
		id: number;
		country: string;
		sunrise: number;
		sunset: number;
	};
	timezone: number;
	visibility: number;
	weather: Array<Iweather>;
	wind: { speed: number, deg: number };
};

// –î–≤–∞ –∑–∞–ø—Ä–æ—Å–∞ –∑–∞ –ø–æ–≥–æ–æ–¥–æ–π –ü–µ—Ä–≤—ã–π - –æ—Å–Ω–æ–≤–Ω–∞—è –ø–æ–≥–æ–¥–∞ –ø–æ –≤—ã–±—Ä–∞–Ω–Ω–æ–º—É –≥–æ—Ä–æ–¥—É, –í—Ç–æ—Ä–æ–π - —Å –≥–µ–æ–ª–æ–∫–∞—Ü–∏–µ–π –¥–ª—è –¥–∞–Ω–Ω—ã—Ö –ø–æ –¥–Ω—è–º –Ω–µ–¥–µ–ª–∏
export const api = {
	getWeather(cityName: string) {
		return instance.get<GetWeatherType>(`weather?q=${cityName}&units=metric&appid=${apiKey}`)
		.then(res => res.data);
	},
	getWeekWeather(lat: number, lon: number) {
		return instance.get(`onecall?lat=${lat}&lon=${lon}&exclude=minutely,hourly,current&appid=${apiKey}`)
		.then(res => res.data)
	}
};