import {
	GET_WEATHER,
	ActionsType,
	getWeatherAC,
	WEEK_WEATHER,
	weekWeatherAC,
	historySearchAC,
	HISTORY_SEARCH
} from "./actions";
import { api } from "../api/apiFetch";
import { Dispatch } from 'redux';
import { date–°onvertation } from "../assets/reusableJS";

export type WeekWeatherType = {
	dt: undefined | string;
	daytime: undefined | string;
	temp_min: number;
	temp_max: number;
	temp_day: number;
};

export type historySearchType = {
	city: any;
	country: any;
	temperature: any;
}

export type StateWeatherType = {
	weather: {
		success: boolean;
		description: undefined | string;
		temperature: undefined | number;
		temperatureChange: undefined;
		city: undefined | string;
		country: undefined | string;
		humidity: undefined | string;
		pressure: undefined | string;
		wind: undefined | string;
		sunrise: undefined | string;
		sunset: undefined | string;
		daytime: undefined | string;
		dt: undefined | string;
		error: undefined | string;
	};
	backgroundDayNight: boolean;
	weekWeather: Array<WeekWeatherType>;
	historySearch: Array<historySearchType>;
};

const initialState = {
	weather: {
		success: false,
		description: undefined,
		temperature: undefined,
		temperatureChange: undefined,
		city: undefined,
		country: undefined,
		humidity: undefined,
		pressure: undefined,
		wind: undefined,
		sunrise: undefined,
		sunset: undefined,
		daytime: undefined,
		dt: undefined,
		error: undefined
	},
	backgroundDayNight: true,
	weekWeather: [],
	historySearch: []
};

export const weatherReducer = (state: StateWeatherType = initialState, action: ActionsType) => {

	switch (action.type) {
		// —Å–æ—Ö—Ä–∞–Ω—è—é –æ—Å–Ω–æ–≤–Ω—ã–µ –¥–∞–Ω–Ω—ã–µ –ø–æ –≤—ã–±—Ä–∞–Ω–Ω–æ–º—É –≥–æ—Ä–æ–¥—É –¥–ª—è –æ—Ç—Ä–∏—Å–æ–≤–∫–∏
		case GET_WEATHER:
			return {
				...state, weather: { ...action.weather }, backgroundDayNight: action.weather.backgroundDayNight
			};
		// —Å–æ—Ö—Ä–∞–Ω—è—é –¥–∞–Ω–Ω—ã–µ –ø–æ –¥–Ω—è–º –Ω–µ–¥–µ–ª–∏
		case WEEK_WEATHER:
			return {
				...state, weekWeather: [...action.weekWeather]
			};
		// —Å–æ—Ö—Ä–∞–Ω—è—é –¥–∞–Ω–Ω—ã–µ –ø–æ –∏—Å—Ç–æ—Ä–∏–∏ –ø–æ–∏—Å–∫–∞
		case HISTORY_SEARCH:

			return {
				...state, historySearch: action.historySearch
			};

		default:
			return state;
	};
};

export const getWeatherTC = (city: string) => async (dispatch: Dispatch) => {
	try {
		let historySearchLS = [];
		if (!localStorage.getItem('historySearchLS')) {
			localStorage.setItem("historySearchLS", JSON.stringify([]));
		} else {
			historySearchLS = JSON.parse(localStorage.getItem('historySearchLS') || '[]');
		}
		// –°–æ–∑–¥–∞—é –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –¥–ª—è –ª–æ–∫–∞–ª —Å—Ç–æ—Ä–µ–¥–∂–∞, –¥–µ–ª–∞—é –∑–∞–ø—Ä–æ—Å –∑–∞ –≤—ã–±—Ä–∞–Ω–Ω—ã–º –≥–æ—Ä–æ–¥–æ–º –∏ –∑–∞–ø—Ä–æ—Å –¥–ª—è –¥–Ω–µ–π –Ω–µ–¥–µ–ª–∏
		const data = await api.getWeather(city);
		const weekWeather = await api.getWeekWeather(data.coord.lat, data.coord.lon);
		// –°–æ–∑–¥–∞—é –ø–µ—Ä–µ–º–µ–Ω–Ω—ã–µ –¥–ª—è —É–¥–æ–±—Å—Ç–≤–∞ –æ–±—Ä–∞–±–æ—Ç–∫–∏ –¥–∞–Ω–Ω—ã—Ö –≤ –¥–∞–ª—å–Ω–µ–π—à–µ–º
		const sunrise = date–°onvertation(data.sys.sunrise);
		const sunset = date–°onvertation(data.sys.sunset);
		const dayTime = date–°onvertation(data.dt);
		// –°–æ–∑–¥–∞—é –æ–±—ä–µ–∫—Ç, —á—Ç–æ –±—É–¥–µ—Ç —Ö—Ä–∞–Ω–∏—Ç—å—Å—è –≤ –ª–æ–∫–∞–ª —Å—Ç–µ–π—Ç–µ
		const newData = {
			success: true,
			description: data.weather[0].description,
			temperature: Math.round(data.main.temp),
			temperatureChange: {
				temp_min: Math.round(data.main.temp_min),
				temp_max: Math.round(data.main.temp_max)
			},
			city: data.name,
			country: data.sys.country,
			humidity: data.main.humidity,
			pressure: data.main.pressure,
			wind: data.wind.speed,
			sunrise: `${sunrise.hour}:${sunrise.minute}`,
			sunset: `${sunset.hour}:${sunset.minute}`,
			daytime: `${dayTime.hour}:${dayTime.minute}`,
			dt: data.dt,
			error: undefined,
			backgroundDayNight: data.dt > data.sys.sunrise && data.dt < data.sys.sunset
		};

		// –°–æ–∑–¥–∞—é –º–∞—Å—Å–∏–≤ —Å –æ–±—ä–µ–∫—Ç–∞–º–∏ –ø–æ –¥–Ω—è–º –Ω–µ–¥–µ–ª–∏
		const weekWeatherData = weekWeather.daily.map((el: any) => {
			return {
				dt: el.dt,
				weather: el.weather[0].main,
				tempMax: el.temp.max,
				tempMin: el.temp.min
			}
		})
		// –°–æ–∑–¥–∞—é –æ–±—ä–µ–∫—Ç –¥–ª—è —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏—è –Ω–∞–π–¥–µ–Ω–Ω–æ–≥–æ –≥–æ—Ä–æ–¥–∞
		const historySearchData = {
			city: data.name,
			country: data.sys.country,
			temperature: Math.round(data.main.temp)
		};
		// –°–æ—Ö—Ä–∞–Ω—è—é –¥–∞–Ω–Ω—ã–µ –≤ –ª–æ–∫–∞–ª—Å—Ç–æ—Ä–µ–¥–∂ –∏ –∑–∞–ø—É—Å–∫–∞—é —Ç—Ä–∏ –¥–∏—Å–ø–∞—Ç—á–∞ –¥–ª—è –æ–±–Ω–æ–≤–ª–µ–Ω–∏—è –¥–∞–Ω–Ω—ã—Ö –≤ –∏–Ω–∏—Ü–∞–ª–∏–∑–∏—Ä–æ–≤–∞–Ω–Ω–æ–º —Å—Ç–µ–π—Ç–µ
		!historySearchLS ? 
		localStorage.setItem('historySearchLS', JSON.stringify([historySearchData])) :
		localStorage.setItem('historySearchLS', JSON.stringify([historySearchData, ...historySearchLS]));
		dispatch(getWeatherAC(newData));
		dispatch(weekWeatherAC(weekWeatherData));
		historySearchLS ? dispatch(historySearchAC([historySearchData, ...historySearchLS])) : dispatch(historySearchAC([historySearchData]));
	} catch (error) {
		return error;
	}
};